import React from 'react';
import {Link} from 'react-router-dom';

function navbar() {
    return (
        <>
        <nav className="navbar"></nav>
            <div className="navbar-container">
                <Link to="/" className="navbarlogo">
                    TRVL <i className="fab fa-typo3"></i>
                </Link>
            </div>
       </>
    );
}
export default navbar;